}

.corousel {
    position: relative;
    height: 530px;
    max-width: 100%;
    overflow: hidden;
}

p {
    margin: 0 0 10px;
}

.welcome-section .welcome-content p {
    margin: 0;
}

.welcome-section {
    background: #F5F6FA;
}

.welcome-section .welcome-content .title p {
    color: #A40D12;
    font-family: 'Pacifico';
    font-size: 20px;
}

.sec-pad {
    padding: 20px 0;
}

.welcome-section .welcome-content p {
    color: #757575;
}

.welcome-section .welcome-content {
    padding-left: 56px;
}

.welcome-section .welcome-content .title {
    margin-bottom: 10px;
}

.recent-projects .single-recent-project .img-box:before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(33, 33, 33, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: all .4s ease;
}

.sec-title {
    margin-top: -8px;
    margin-bottom: 55px;
}

.text-center {
    text-align: center;
}

.recent-projects .single-recent-project .img-box {
    position: relative;
    overflow: hidden;
}

.album .img-box img {
    height: 230px !important;
    display: block;
    margin: 0px auto !important;
}

.recent-projects .single-recent-project .text-box {
    position: relative;
    padding-left: 0px;
}

.recent-projects .single-recent-project .text-box a.more {
    width: 42px;
    height: 42px;
    background: #A40D12;
    color: #fff;
    font-size: 14px;
    line-height: 42px;
    border-radius: 50%;
    text-align: center;
    position: absolute;
    top: -21px;
    right: 30px;
    z-index: 9;
    transition: all .4s ease;
}

.recent-projects .single-recent-project .img-box>img {
    width: 100%;
}

.recent-projects .single-recent-project .text-box .inner {
    background: #F5F6FA;
    padding: 32.5px 0;
    padding-left: 0px;
    display: block;
}

.hvr-bounce-to-bottom {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    position: relative;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: .5s;
    transition-duration: .5s;
}

.recent-projects .single-recent-project .text-box .inner:before {
    background: #A40D12;
}

.hvr-bounce-to-bottom:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #2098D1;
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: .5s;
    transition-duration: .5s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.recent-projects .single-recent-project .text-box span {
    color: #A40D12;
    font-family: 'Pacifico' !important;
    font-size: 16px;
}

.footer-bottom {
    background: #171717;
}

.footer-bottom .thm-container {
    padding: 15px 15px;
}

.thm-container {
    width: 100%;
    max-width: 1200px;
    padding-left: 15px !important;
    padding-right: 15px !important;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.footer-bottom .thm-container .copy-text p {
    margin: 0;
    color: #A0A0A0;
    font-size: 16px;
}

.pull-right {
    float: right!important;
}

.footer-bottom .thm-container .social-box a.fa-twitter {
    background: #FEC931;
}

.footer-bottom .thm-container .social-box a {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    color: #fff;
    font-size: 18px;
    line-height: 59px;
    text-align: center;
    line-height: 30px;
}

.hvr-pulse {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
}

.pull-left {
    float: left!important;
}

.footer-bottom .thm-container .social-box a+a {
    margin-left: 10px;
}

.footer-bottom .thm-container .social-box a.fa-pinterest {
    background: #FFFFFF;
    color: #212121;
}

.footer-bottom .thm-container .social-box a.fa-facebook-f {
    background: #2884C6;
}

.footer-bottom .thm-container .social-box a.fa-youtube {
    background: #A40D12;
}

.footer-bottom {
    background: #171717;
}

.footer-bottom .thm-container .copy-text p {
    margin: 0;
    color: #A0A0A0;
    font-size: 16px;
}

#minimal-bootstrap-carousel .carousel-caption {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    text-align: right;
    text-shadow: none;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container {
    display: table;
    width: 100%;
    height: 100%;
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container .box {
    display: table-cell;
    text-align: left;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container .box.valign-top {
    vertical-align: top;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container .box.valign-bottom {
    vertical-align: bottom;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container .box.valign-middle {
    vertical-align: middle;
}

#minimal-bootstrap-carousel .carousel-caption .thm-container .box .content {
    display: block;
}

#minimal-bootstrap-carousel .carousel-control {
    background: none;
    width: 70px;
    height: 70px;
    font-size: 20px;
    line-height: 70px;
    border-radius: 50%;
    color: #fff;
    background: rgba(0, 0, 0, 0.1);
    visibility: visible !important;
    opacity: 1;
    margin-top: -20px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    top: 50%;
    z-index: 99;
}